'use strict';

Vue.component('blog-post', {
    props: ['post'],
    template: '\n        <div class="list-item" v-bind:class="{ active: isActive }">\n            <span class="list-item-wrapper" v-on:click="activate">\n                <span class="image"><span class="zekken-logo"></span></span>\n                <span class="content">{{ trimmedContent }}</span>\n                <span class="meta">\n                    <span class="author">{{ post.author }}</span>\n                    <span class="date">{{ formattedDate }}</span>         \n                </span>\n            </span>\n            <span class="controls">\n                <span class="control favourite" v-bind:class="{ favourited: isFavourite, loading: isFavouriting }" v-on:click="favourite"></span>\n                <span class="control remove" v-on:click="deactivate"></span>\n            </span>\n        </div>\n    ',
    data: function data() {
        return {
            isActive: false,
            isFavourite: this.post.isFavourite || false,
            isFavouriting: this.post.isFavourite || false
        };
    },
    methods: {
        activate: function activate() {
            this.isActive = true;
        },
        deactivate: function deactivate() {
            this.isActive = false;
        },
        favourite: function favourite() {
            this.isFavourite = !this.isFavourite;
            this.isFavouriting = !this.isFavouriting;
        }
    },
    computed: {
        trimmedContent: function trimmedContent() {
            return this.post.content.substring(0, 120) + '...';
        },
        formattedDate: function formattedDate() {
            return moment(this.post.date).fromNow();
        }
    }
});

Helper.getJson('store/sample-posts.json', function (data) {
    Helper.loaded(document.getElementById('app-loader'), function () {
        var app = new Vue({
            el: '#app',
            data: data
        });
    });
});